input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  @apply text-black;
  opacity: 1;
}

input::-moz-placeholder, textarea::-moz-placeholder {
  /* Firefox 19+ */
  @apply text-black;
  opacity: 1;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  /* IE 10+ */
  @apply text-black;
  opacity: 1;
}

input:-moz-placeholder, textarea:-moz-placeholder {
  /* Firefox 18- */
  @apply text-black;
  opacity: 1;
}

input:not(.ui-spinner-input) {
  width: 100%;
  height: 55px;
  padding: 0 22px;
  border-radius: 0;
  box-sizing: border-box;
  outline: 0 !important;
  box-shadow: none !important;
  appearance: none;
  @apply bg-gray-100 text-sm;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 15px 22px;
  border-radius: 0;
  box-shadow: none !important;
  box-sizing: border-box;
  outline: 0 !important;
  appearance: none;
  @apply bg-gray-100 text-sm;
}

input[type=radio] {
  position: absolute;
  opacity: 0;
  left: -999999px;
  cursor: pointer;
  height: auto;
  appearance: none;
}
input[type=radio] + label:not(.ui-widget) {
  display: block;
  position: relative;
  padding-left: 1.5rem;
  box-sizing: border-box;
  cursor: pointer;
}
input[type=radio] + label:not(.ui-widget):before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 3px;
  left: 0;
  margin: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;
  @apply bg-gray-100;
}
input[type=radio]:checked + label:not(.ui-widget):after {
  content: "";
  display: inline-block;
  position: absolute;
  left: 5px;
  top: 8px;
  margin: 0;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  vertical-align: middle;
  @apply bg-black;
}

input[type=checkbox] {
  position: absolute;
  opacity: 0;
  left: -999999px;
  cursor: pointer;
  height: auto;
}
input[type=checkbox] + label {
  display: block;
  position: relative;
  padding-left: 1.5rem;
  box-sizing: border-box;
  cursor: pointer;
}
input[type=checkbox] + label:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 3px;
  left: 0;
  width: 15px;
  height: 15px;
  border-radius: 0;
  box-sizing: border-box;
  cursor: pointer;
  @apply bg-gray-100;
}
input[type=checkbox]:checked + label:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: auto;
  margin: auto 0;
  left: 5px;
  top: 5px;
  width: 5px;
  height: 9px;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  @apply border-black;
}

select {
  width: 100%;
  height: 55px;
  padding: 0 45px 0 22px;
