.burger {
  cursor: pointer;
  margin-left: auto;
}
.burger.open .burger__lines {
  background: rgba(255, 255, 255, 0);
}
.burger.open .burger__lines:after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  bottom: 0;
}
.burger.open .burger__lines:before {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  top: 0;
}
.burger__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.burger__menu {
  position: relative;
  width: 38px;
  height: 25px;
  z-index: 27;
  cursor: pointer;
}
.burger__lines {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  top: 11px;
  right: 0;
  opacity: 1;
  border-radius: 6px;
}
.burger__lines:before {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  top: 11px;
  left: 0;
  content: "";
  display: block;
  border-radius: 6px;
}
.burger__lines:after {
  position: absolute;
  background: white;
  width: 100%;
  height: 3px;
  bottom: 11px;
  left: 0;
  content: "";
  display: block;
  border-radius: 6px;
}
.burger__lines, .burger__lines::after, .burger__lines::before {
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
}

.mobile-navigation {
