.page-header {
    position: relative;
    z-index: 1;

    &__image-wrap {
        position: relative;
        z-index: 1;
        border-bottom-left-radius: 65px;

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 65px;
            left: 22%;
            z-index: -1;
            width: 100%;
            height: 100%;
            border-bottom-left-radius: inherit;
            transform: rotate(-23deg);
            background: inherit;
        }
    }

    &__image {
        position: relative;
        z-index: 1;
        width: 50vw;
        height: 220px;
        border-bottom-left-radius: inherit;
    }

}